import React from 'react';
import './SearchForm.scss';
import { SearchInput } from './searchInput';

export class SearchForm extends React.Component {
    state = {
        currentValue: '',
    }

    onChange = (currentValue) => {
        this.setState({currentValue: currentValue});
    }

    onSubmit = (e) => {
        e.preventDefault();
    }

    onClick = (cityName, cityKey) => {//Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð³Ð¾ÑÐ¾Ð´Ð° Ð² Ð¸Ð·Ð±ÑÐ°Ð½Ð½ÑÐµ Ð¸ ÑÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð´ÑÐºÐ°Ð·ÐºÐ¸
        this.props.onClick(cityName, cityKey);
        this.setState({currentValue: ''});// ÑÐºÑÑÑÐ¸Ðµ Ð¿Ð¾Ð´ÑÐºÐ°Ð·ÐºÐ¸
    }

    render() {
        return (
            <form className='menu__search search-form' onSubmit={this.onSubmit}>
                <SearchInput 
                    placeholder={this.props.placeholder} 
                    currentValue={this.state.currentValue}
                    onChange={this.onChange}
                    onClick={this.onClick}
                />
            </form>
        )
    }
}